import React, { FC, useState, useRef, useEffect, forwardRef, useImperativeHandle } from 'react'
import { View, Text, Picker, Input } from '@tarojs/components'
import { AtIcon, AtInput } from 'taro-ui'
import Card from '@/components/Card'
import Constant from '@/common/constant'
import classnames from 'classnames'
import WishPrice, { WishPriceRefType } from '../WishPrice'
import WorkingExperiences, { WorkingExperiencesRefType } from '../WorkingExperiences'
import moment from 'moment'
import './index.less'


interface PersonalInfoProps {
  resume,
  ref,
  onNextStep?
}

export interface WorkingExperiencesItemType {
  serviceName: string,
  startTime: string,
  endTime: string,
  experienceDiscription: string,
}
interface formItemType {
  education: string,
  married: string,
  mandarinAble: string,
  height: string,
  religion: string,
  weight: string,
  addressArr: string[],
  address: string,
  wishPrice: string[],
  auntCert: string[],
  WorkingExperiences: WorkingExperiencesItemType[],
  visa: string,
  bloodType: string,
  children: string,
  childrenAge: string,
  spouseJob: string,
  emergencyPhone: string,
}
export interface PersonalInfoRefType {
  data3: Function
}
/**第三步个人情况 */
const PersonalInfo: FC<PersonalInfoProps> = forwardRef((props: PersonalInfoProps, ref: React.MutableRefObject<PersonalInfoRefType>) => {
  let [index, setIndex] = useState(0)
  let [formItem, setformItem] = useState<formItemType>({
    education: '',
    married: '',
    mandarinAble: '',
    height: '',
    religion: '',
    address: '',
    weight: '',
    addressArr: [],
    wishPrice: ['1000', '2000'],
    WorkingExperiences: [],
    auntCert: [],
    visa: '',
    bloodType: '',
    children: '',
    childrenAge: '',
    spouseJob: '',
    emergencyPhone: '',
  })
  let [isAddArr, setIsAddArr] = useState([
    { text: '身高', flag: false, name: 'height' },
    { text: '体重', flag: false, name: 'weight' },
    { text: '宗教信仰', flag: false, name: 'religion' },
    { text: '签证', flag: false, name: 'visa' },
    { text: '血型', flag: false, name: 'bloodType' },
    { text: '子女数量', flag: false, name: 'children' },
    { text: '子女年龄', flag: false, name: 'childrenAge' },
    { text: '配偶职业', flag: false, name: 'spouseJob' },
    { text: '紧急联系信息', flag: false, name: 'emergencyPhone' },
  ])
  const WishPriceRef: React.MutableRefObject<WishPriceRefType> = useRef({ show() { } });
  const WorkingExperiencesRef: React.MutableRefObject<WorkingExperiencesRefType> = useRef({ show() { }, edit() { } });
  // 抛出方法
  useImperativeHandle(ref, () => ({
    data3: () => submit()
  }));

  useEffect(() => {
    let { ResumeDetail } = props.resume;
    setformItem({
      education: ResumeDetail.detail?.education,
      married: ResumeDetail.detail?.married,
      mandarinAble: ResumeDetail.skills?.mandarinAble,
      height: ResumeDetail.detail?.height,
      religion: ResumeDetail.detail?.religion,
      address: ResumeDetail.address?.address,
      weight: ResumeDetail.detail?.weight,
      auntCert: ResumeDetail.skills?.auntCert || [],
      addressArr: ResumeDetail.address?.province ? [ResumeDetail.address?.province, ResumeDetail.address?.city, ResumeDetail.address?.district] : [],
      wishPrice: [ResumeDetail.skills?.minExpectSalary || '1000', ResumeDetail.skills?.maxExpectSalary || '2000'],
      WorkingExperiences: ResumeDetail.workingExperiencesList || [],
      visa: ResumeDetail.detail?.visa,
      bloodType: ResumeDetail.detail?.bloodType,
      children: ResumeDetail.detail?.children,
      childrenAge: ResumeDetail.detail?.childrenAge,
      spouseJob: ResumeDetail.detail?.spouseJob,
      emergencyPhone: ResumeDetail.detail?.emergencyPhone,
    })
    if (formItem.height) {
      isAddArr[0].flag = true
    }
    if (formItem.weight) {
      isAddArr[1].flag = true
    }
    if (formItem.religion) {
      isAddArr[2].flag = true
    }
    if (formItem.visa) {
      isAddArr[3].flag = true
    }
    if (formItem.bloodType) {
      isAddArr[4].flag = true
    }
    if (formItem.children) {
      isAddArr[5].flag = true
    }
    if (formItem.childrenAge) {
      isAddArr[6].flag = true
    }
    if (formItem.spouseJob) {
      isAddArr[7].flag = true
    }
    if (formItem.emergencyPhone) {
      isAddArr[8].flag = true
    }
    setIsAddArr([...isAddArr])

  }, [props.resume.ResumeDetail])

  //修改表单值
  function handleChange(name, value) {
    if (name == 'auntCert') {
      formItem[name].indexOf(value) > -1 ? setformItem({ ...formItem, [name]: formItem[name].filter(item => item != value) }) : setformItem({ ...formItem, [name]: [...formItem[name], value] })
    } else {
      setformItem({ ...formItem, [name]: value })
    }
  }
  function handleChangeRadio(name, value) {
    formItem[name] == value ? setformItem({ ...formItem, [name]: '' }) : setformItem({ ...formItem, [name]: value })
  }
  //切换个人能力
  function changeIsAddArr(index) {
    if (isAddArr[index].flag) {
      setformItem((prev) => {
        return ({ ...prev, [isAddArr[index].name]: null })
      })
    }
    isAddArr[index].flag = !isAddArr[index].flag;
    setIsAddArr([...isAddArr])
  }


  function onConfirm(res, isAdd) {
    const WorkingExperiences = formItem.WorkingExperiences;
    if (isAdd) {
      WorkingExperiences.push(res)
    } else {
      WorkingExperiences[index] = res
    }
    setformItem({ ...formItem, WorkingExperiences: WorkingExperiences })
  }

  function delItem(index) {
    const WorkingExperiences = formItem.WorkingExperiences.filter((item, i) => i !== index)
    setformItem({ ...formItem, WorkingExperiences: WorkingExperiences })
  }

  function submit() {
    let params = {
      address: formItem.address,
      city: formItem.addressArr?.[1],
      district: formItem.addressArr?.[2],
      province: formItem.addressArr?.[0],
      education: formItem.education,
      height: formItem.height,
      married: formItem.married,
      religion: formItem.religion,
      weight: formItem.weight,
      auntCert: formItem.auntCert,
      mandarinAble: formItem.mandarinAble,
      maxExpectSalary: formItem.wishPrice[1],
      minExpectSalary: formItem.wishPrice[0],
      workingExperiencesList: formItem.WorkingExperiences,
      visa: formItem.visa,
      bloodType: formItem.bloodType,
      children: formItem.children,
      childrenAge: formItem.childrenAge,
      spouseJob: formItem.spouseJob,
      emergencyPhone: formItem.emergencyPhone,
    }
    return params;
  }

  return (
    <View className='index'>
      <View className="personalInfo">
        <Card title="学历">
          <View className='at-row at-row--wrap checkboxView'>
            {props.resume.op.education.map((item) => {
              return <View className='at-col at-col-4' key={item}><View
                className={classnames({
                  checkbox: true,
                  checkboxActive: formItem.education === item
                })}
                onClick={() => { handleChangeRadio('education', item) }}><Text>{item}</Text></View></View>
            })}
          </View>
        </Card>
        <Card title="婚姻">
          <View className='at-row at-row--wrap'>
            {props.resume.op.marriedList.map((item) => {
              return <View className='at-col at-col-4 checkboxView' key={item}>
                <View
                  className={classnames({
                    checkbox: true,
                    checkboxActive: formItem.married == item
                  })}
                  onClick={() => { handleChangeRadio('married', item) }}>{item}</View>
              </View>
            })}
          </View>
        </Card>
        <Card title="普通话水平">
          <View className='at-row at-row--wrap'>
            {props.resume.op.mandarinAbleList.map((item) => {
              return <View className='at-col at-col-4 checkboxView' key={item}>
                <View
                  className={classnames({
                    checkbox: true,
                    checkboxActive: formItem.mandarinAble === item
                  })}
                  onClick={() => { handleChangeRadio('mandarinAble', item) }}>{item}</View>
              </View>
            })}
          </View>
        </Card>
        <View className="CardSelect" onClick={() => { WishPriceRef.current.show(formItem.wishPrice) }}>
          <View className="CardSelectTitle">期望薪资(元)</View>
          <View className="CardSelectValue">{formItem.wishPrice && formItem.wishPrice.join('-')}<AtIcon value='chevron-right' size='16' color='#ccc'></AtIcon></View>
        </View>
        <Card title="专业证书" height={200}>
          <View className='at-row at-row--wrap checkboxView'>
            {props.resume.opCustom.auntCertTextListCustom.map((item) => {
              return <View className='at-col at-col-4' key={item}><View
                className={classnames({
                  checkbox: true,
                  checkboxActive: formItem.auntCert.includes(item)
                })}
                onClick={() => { handleChange('auntCert', item) }}><Text>{item}</Text></View></View>
            })}
          </View>
        </Card>
        <Card title="工作经历">
          <View className="pic">
            <View className="picList">
              {
                formItem.WorkingExperiences.map((item, index) => (
                  <View className="picListItem" key={index}>
                    <View className="picListItemTime">{moment(item.startTime).format('YYYY-MM-DD')}至{moment(item.endTime).format('YYYY-MM-DD')}</View>
                    <View className="picListItemBtn">
                      <Text className="Text" onClick={() => { setIndex(index); WorkingExperiencesRef.current.edit(item) }}>编辑</Text>
                      <Text className="delText" onClick={() => { delItem(index) }}>删除</Text>
                    </View>
                  </View>
                ))
              }
            </View>
            <View className={classnames({
              picTitle: true,
              picEmpty: formItem.WorkingExperiences.length == 0
            })} onClick={() => { WorkingExperiencesRef.current.show() }}>
              <Text className="iconfont icontianjiazhaopian picIcon"></Text>
              <Text>添加工作经历</Text>
            </View>
          </View>
        </Card>
        <View className="CardSelect CardSelect2">
          <View className="CardSelectTop">
            <View className="CardSelectTitle">现住地址</View>
            <Picker onChange={(e) => { handleChange('addressArr', e.detail.value) }} mode='region' value={formItem.addressArr} className="CardSelectValue">
              <View >{formItem.addressArr.join('-')}<AtIcon value='chevron-right' size='16' color='#ccc'></AtIcon></View>
            </Picker>
          </View>
          <AtInput
            className="CardSelectInput"
            name='address'
            placeholder='请输入详细地址'
            value={formItem.address}
            onChange={(value) => { handleChange('address', value); }}
          />
        </View>

        <View className="cardInput cardInputWarp" style={{ display: isAddArr[0].flag ? 'flex' : 'none' }}>
          <Text className="leftText">身高</Text>
          <Input
            className="cinput"
            name='height'
            type='number'
            placeholder='请输入身高'
            value={formItem.height}
            maxlength={4}
            placeholderClass="cplaceholder"
            onInput={(value) => { handleChange('height', value.detail.value) }}
          />
          <Text className="rightText">cm</Text>
        </View>

        <View className="cardInput cardInputWarp" style={{ display: isAddArr[1].flag ? 'flex' : 'none' }}>
          <Text className="leftText">体重</Text>
          <Input
            className="cinput"
            name='weight'
            type='number'
            placeholder='请输入体重'
            maxlength={4}
            value={formItem.weight}
            placeholderClass="cplaceholder"
            onInput={(value) => { handleChange('weight', value.detail.value) }}
          />
          <Text className="rightText">kg</Text>
        </View>

        <View className="cardInput cardInputWarp" style={{ display: isAddArr[2].flag ? 'flex' : 'none' }}>
          <Text className="leftText">宗教信仰</Text>
          <Input
            className="cinput"
            name='religion'
            placeholder='请输入宗教信仰'
            value={formItem.religion}
            placeholderClass="cplaceholder"
            onInput={(value) => { handleChange('religion', value.detail.value) }}
          />
        </View>

        {isAddArr[3].flag &&
          <Card title="签证">
            <View className='at-row at-row--wrap checkboxView'>
              {props.resume.op.visaList.map((item) => {
                return <View className='at-col at-col-4' key={item}><View
                  className={classnames({
                    checkbox: true,
                    checkboxActive: formItem.visa === item
                  })}
                  onClick={() => { handleChangeRadio('visa', item) }}><Text>{item}</Text></View></View>
              })}
            </View>
          </Card>}
        {isAddArr[4].flag &&
          <Card title="血型">
            <View className='at-row at-row--wrap checkboxView'>
              {props.resume.op.bloodTypeList.map((item) => {
                return <View className='at-col at-col-4' key={item}><View
                  className={classnames({
                    checkbox: true,
                    checkboxActive: formItem.bloodType === item
                  })}
                  onClick={() => { handleChangeRadio('bloodType', item) }}><Text>{item}</Text></View></View>
              })}
            </View>
          </Card>}

        <View className="cardInput cardInputWarp" style={{ display: isAddArr[5].flag ? 'flex' : 'none' }}>
          <Text className="leftText">子女数量</Text>
          <Input
            className="cinput"
            name='children'
            type='number'
            placeholder='请输入子女数量'
            value={formItem.children}
            maxlength={2}
            placeholderClass="cplaceholder"
            onInput={(value) => { handleChange('children', value.detail.value) }}
          />
        </View>

        <View className="cardInput cardInputWarp" style={{ display: isAddArr[6].flag ? 'flex' : 'none' }}>
          <Text className="leftText">子女年龄</Text>
          <Input
            className="cinput"
            name='childrenAge'
            type='number'
            placeholder='请输入子女年龄'
            value={formItem.childrenAge}
            maxlength={2}
            placeholderClass="cplaceholder"
            onInput={(value) => { handleChange('childrenAge', value.detail.value) }}
          />
        </View>

        <View className="cardInput cardInputWarp" style={{ display: isAddArr[7].flag ? 'flex' : 'none' }}>
          <Text className="leftText">配偶职业</Text>
          <Input
            className="cinput"
            name='spouseJob'
            placeholder='请输入配偶职业'
            value={formItem.spouseJob}
            maxlength={10}
            placeholderClass="cplaceholder"
            onInput={(value) => { handleChange('spouseJob', value.detail.value) }}
          />
        </View>

        <View className="cardInput cardInputWarp" style={{ display: isAddArr[8].flag ? 'flex' : 'none' }}>
          <Text className="leftText">紧急联系方式</Text>
          <Input
            className="cinput"
            name='emergencyPhone'
            placeholder='请输入紧急联系方式'
            value={formItem.emergencyPhone}
            maxlength={20}
            placeholderClass="cplaceholder"
            onInput={(value) => { handleChange('emergencyPhone', value.detail.value) }}
          />
        </View>

        <View className="addCard">
          <Text className="addCardTitle">添加其他信息</Text>
          <View className="addCardView at-row at-row--wrap">
            {isAddArr.map((item, index) => {
              return (<View className="at-col at-col-4" onClick={() => { changeIsAddArr(index) }} key={item.text}>
                <View className={classnames({
                  addCardItem: 'true',
                  activeAdd: item.flag
                })}>
                  <Text className="iconfont icontianjiashenfenzheng addCardItemIcon"></Text>
                  <Text className="addCardItemText">{item.text}</Text>
                </View>
              </View>)
            })}
          </View>
        </View>
        <WishPrice ref={WishPriceRef} onConfirm={(res) => { handleChange('wishPrice', res) }} />
        <WorkingExperiences ref={WorkingExperiencesRef} onConfirm={onConfirm} resume={props.resume} />
      </View>
    </View>
  )
})
export default PersonalInfo
