import React, { FC, useState, useEffect, useImperativeHandle, forwardRef } from 'react'
import { View, Text, Picker } from '@tarojs/components'
import { AtTextarea, AtIcon } from 'taro-ui'
import Card from '@/components/Card'
import UploadImg from '@/components/UploadImg'
import UploadVideo from '@/components/UploadVideo'
import UploadAvatar from '@/components/UploadAvatar'
import classnames from 'classnames'
import tip from '@/utils/tip'
import './index.less'

interface JobInfoProps {
  resume,
  ref,
  onNextStep?,
  dispatch
}

interface convertMap2ListType {
  name: string,
  id: string
}

interface formItemType {
  auntHeadPhoto: string,
  certPhotos: string[],
  photos: string[],
  videos: string[],
  cookAble: string,
  cuisine: string[],
  language: string[],
  workTime: string,
  workingLife: number,
  jobIntensionsList: string[],
  description: string,
  composite: string[],
  innerPhotos: string[],
}

export interface JobInfoRefType {
  data2: Function,
}

/**第二步求职信息 */
const JobInfo: FC<JobInfoProps> = forwardRef((props: JobInfoProps, ref: React.MutableRefObject<JobInfoRefType>) => {

  let [formItem, setformItem] = useState<formItemType>({
    workTime: "",
    cookAble: "",
    auntHeadPhoto: "",
    description: "",
    workingLife: 0,
    jobIntensionsList: [],
    cuisine: [],
    language: [],
    photos: [],
    certPhotos: [],
    videos: [],
    composite: [],
    innerPhotos: [],
  });

  let [isAddArr, setIsAddArr] = useState([
    { text: '做饭能力', flag: false }, { text: '掌握菜系', flag: false }, { text: '语言能力', flag: false }
  ]);

  // 抛出方法
  useImperativeHandle(ref, () => ({
    data2: () => submit()
  }));

  useEffect(() => {
    let { ResumeDetail } = props.resume;
    setformItem({
      jobIntensionsList: ResumeDetail.jobIntensionsList || [],
      workTime: ResumeDetail.workTime || '',
      cookAble: ResumeDetail.skills?.cookAble || '',
      workingLife: ResumeDetail.skills?.workingLife ? props.resume.op.WorkingLifeDataArr.findIndex((item: convertMap2ListType) => item.id == ResumeDetail.skills?.workingLife) : 0,
      cuisine: ResumeDetail.skills?.cuisine || [],
      language: ResumeDetail.skills?.language || [],
      photos: ResumeDetail.photoVideo?.photos || [],
      certPhotos: ResumeDetail.photoVideo?.certPhotos || [],
      videos: ResumeDetail.photoVideo?.videos || [],
      auntHeadPhoto: ResumeDetail.auntHeadPhoto || '',
      description: ResumeDetail.description?.description || '',
      composite: ResumeDetail.skills?.composite || [],
      innerPhotos: ResumeDetail.photoVideo?.innerPhotos || [],
    })

    if (formItem.language.length > 0) {
      isAddArr[2].flag = true
    }
    if (formItem.cookAble.length > 0) {
      isAddArr[0].flag = true
    }
    if (formItem.cuisine.length > 0) {
      isAddArr[1].flag = true
    }
    setIsAddArr([...isAddArr])
  }, [props.resume.ResumeDetail])

  function submit() {
    if (formItem.jobIntensionsList.length == 0) {
      tip.showTips('请选择求职工种')
      return
    }
    if (!formItem.workTime) {
      tip.showTips('请选择工作时间')
      return
    }

    if (formItem.workingLife == null) {
      tip.showTips('请选择工作经验')
      return
    }

    let { ResumeDetail } = props.resume;
    const params = {
      auntHeadPhoto: formItem.auntHeadPhoto,
      description: formItem.description,
      jobIntensionsList: formItem.jobIntensionsList,
      photoVideo: {
        certPhotos: formItem.certPhotos,
        innerPhotos: formItem.innerPhotos,
        photos: formItem.photos,
        videos: formItem.videos
      },
      cookAble: formItem.cookAble,
      cuisine: formItem.cuisine,
      language: formItem.language,
      composite: formItem.composite,
      workTime: formItem.workTime,
      workingLife: props.resume.op.WorkingLifeDataArr[formItem.workingLife].id,
      jobStatus: ResumeDetail.jobStatus,
      markList: ResumeDetail.markList,
      workType: ResumeDetail.workType,
    }
    return params;
  }

  //修改表单值
  function handleChange(name, value) {
    Array.isArray(formItem[name]) ? formItem[name].indexOf(value) > -1 ? setformItem({
      ...formItem,
      [name]: formItem[name].filter(item => item != value)
    }) : setformItem({ ...formItem, [name]: [...formItem[name], value] }) : setformItem({ ...formItem, [name]: value })
  }

  function jobIntensionsListChange(name, value) {
    // const name = 'jobIntensionsList';
    if (formItem[name].indexOf(value) > -1) {
      name != 'composite' && formItem[name].length == 1 ?
        tip.showTips('至少选择一个工种') :
        setformItem({ ...formItem, [name]: formItem[name].filter(item => item != value) })
    } else {
      setformItem({ ...formItem, [name]: [...formItem[name], value] })
    }
  }

  function uploadImgChange(name, img) {
    setformItem({ ...formItem, [name]: img })
  }

  function onFullscreenChange(fullscreen: boolean) {
    props.dispatch({
      type: 'resume/setFullscreen', payload: fullscreen
    })
  }

  return (
    <View className='index'>
      <View className="jobInfo">
        <Card title="求职工种" required height={200}>
          <View className='at-row at-row--wrap checkboxView'>
            {props.resume.JobIntensionsList.map((item: convertMap2ListType) => {
              return <View className='at-col at-col-4' key={item.id}><View
                className={classnames({
                  checkbox: true,
                  checkboxActive: formItem.jobIntensionsList.includes(item.name)
                })}
                onClick={() => {
                  jobIntensionsListChange('jobIntensionsList', item.name)
                }}><Text>{item.name}</Text></View></View>
            })}
          </View>
        </Card>
        <Card title="工作时间" required>
          <View className='at-row at-row--wrap'>
            {props.resume.op.workTimeList.map((item) => {
              return <View className='at-col at-col-4 checkboxView' key={item}>
                <View
                  className={classnames({
                    checkbox: true,
                    checkboxActive: formItem.workTime === item
                  })}
                  onClick={() => {
                    handleChange('workTime', item)
                  }}>{item}</View>
              </View>
            })}
          </View>
        </Card>
        <Picker onChange={(e) => {
          handleChange('workingLife', e.detail.value)
        }} value={formItem.workingLife} range={props.resume.op.WorkingLifeDataArr} mode='selector' rangeKey="name">
          <View className="CardSelect">
            <View className="CardSelectTitle">工作经验<Text className="required">(必填)</Text></View>
            <View
              className="CardSelectValue">{props.resume.op.WorkingLifeDataArr.map((item: convertMap2ListType) => item.name)[formItem.workingLife]}
              <AtIcon value='chevron-right' size='16' color='#ccc' /></View>
          </View>
        </Picker>
        <Card title="自我简介">
          <AtTextarea value={formItem.description} maxLength={1000} className="textarea" placeholder="请输入自我简介"
            onChange={(e) => {
              handleChange('description', e)
            }} />
        </Card>
        <Card>
          <View className="userPic">
            <View className="userPicLeft">
              <Text className="userPicTitle">头像照片</Text>
              <Text className="userPicInfo">建议使用个人证件照</Text>
            </View>
            <View className="userPicRight">
              <UploadAvatar propsfileList={formItem.auntHeadPhoto} onChange={(img) => {
                uploadImgChange('auntHeadPhoto', img)
              }} />
            </View>
          </View>
        </Card>
        <Card title="照片">
          <UploadImg propsfileList={formItem.photos} onChange={(img) => {
            uploadImgChange('photos', img)
          }} title="添加照片" formData={{ watermark: 1 }} />
        </Card>
        <Card title="视频">
          <UploadVideo title="添加视频" icon="icontianjiashipin" propsfileList={formItem.videos} onChange={(img) => {
            uploadImgChange('videos', img)
          }} onFullscreenChange={onFullscreenChange} />
        </Card>
        <Card title="证书照片">
          <UploadImg propsfileList={formItem.certPhotos} onChange={(img) => {
            uploadImgChange('certPhotos', img)
          }} title="添加证书" icon="icontianjiazhengshu" formData={{ watermark: 1 }} />
        </Card>

        <Card title="添加内部可见照片">
          <UploadImg propsfileList={formItem.innerPhotos} onChange={(img) => {
            uploadImgChange('innerPhotos', img)
          }} title="添加照片" />
        </Card>

        <Card
          title="个人技能" right height={200}
          isAll={formItem.composite.length >= props.resume.opCustom.compositeListCustom.length}
          selectAll={() => {
            setformItem({
              ...formItem,
              composite: formItem.composite.length >= props.resume.opCustom.compositeListCustom.length
                ? []
                : props.resume.opCustom.compositeListCustom.map(item => item)
            })
          }}>
          <View className='at-row at-row--wrap'>
            {
              props.resume.opCustom.compositeListCustom.map((item) => {
                return <View className='at-col at-col-4 checkboxView' key={item}>
                  <View
                    className={classnames({
                      checkbox: true,
                      checkboxActive: formItem.composite.includes(item)
                    })}
                    onClick={() => {
                      jobIntensionsListChange('composite', item)
                    }}>{item}</View>
                </View>
              })}
          </View>
        </Card>

      </View>
    </View>
  )
})
export default JobInfo;
