import React, { FC, useState, useEffect, useRef, forwardRef } from 'react'
import Taro from "@tarojs/taro"
import { connect } from 'react-redux'
import { View, Button, CoverView } from '@tarojs/components'
import { AtTabs } from 'taro-ui'
import NavBar from '@/components/NavBar'
import IdentityInfo, { IdentityInfoRefType } from './components/IdentityInfo'
import JobInfo, { JobInfoRefType, } from './components/JobInfo'
import PersonalInfo, { PersonalInfoRefType } from './components/PersonalInfo'
import classnames from 'classnames'
import tip from '@/utils/tip'
import { parseQuery } from '@/utils/stringUtil'
import './resume.less'
import commonUtil from '@/utils/common';

interface AddResumeProps {
  dispatch,
  resume,
  loading
}

const tabList = [{ title: '身份信息' }, { title: '求职信息' }, { title: '个人情况' }]

const AddResume: FC<AddResumeProps> = forwardRef((props: AddResumeProps) => {

  useEffect(() => {
    props.dispatch({ type: 'resume/getCommonOption' });
    props.dispatch({ type: 'resume/getShopLibrary' });
    const optionsSync = Taro.getCurrentInstance().router!.params;//获取页面参数
    const { scene } = optionsSync;
    console.log(optionsSync, '=========进入小程序')
    let paramId: string;//进入页面传递参数id=6804116092485632
    if (scene) {
      //扫小程序码进入
      const s = decodeURIComponent(scene)
      const p: any = parseQuery(s);
      paramId = p.id;
    } else {
      paramId = optionsSync.id;
    }
    if (paramId) {
      props.dispatch({ type: 'common/contentMapping', payload: { id: paramId } }).then((res) => {
        const { content } = res || {};
        const p: any = parseQuery(content);
        commonUtil.setCompanyId(p.companyId);
        commonUtil.setShopId(p.shopId);
        let token = commonUtil.getToken();
        if (!token) {
          //携带进入login页面标示
          Taro.navigateTo({
            url: '/pages/login/login?target=addResume',
          });
        }
        getDetail();
      })
    } else {
      getDetail()
    }

  }, [])

  const getDetail = () => {
    if (commonUtil.getCompanyId()) {
      props.dispatch({
        type: 'resume/queryJobIntensionsListDictionary', payload: {}
      }).then(() => {
        props.dispatch({
          type: 'resume/queryResumeDetail', payload: {}
        })
      })
    }
  }

  const IdentityInfoRef: React.MutableRefObject<IdentityInfoRefType> = useRef({
    data1() {
    }
  });
  const JobInfoRef: React.MutableRefObject<JobInfoRefType> = useRef({
    data2() {
    }
  });
  const PersonalInfoRef: React.MutableRefObject<PersonalInfoRefType> = useRef({
    data3() {
    }
  });

  let [current, setCurrent] = useState(0);

  function nextStep() {
    setCurrent(current + 1);
    Taro.pageScrollTo({
      scrollTop: 0,
      duration: 0,
    })
  }

  function submit() {
    let data1 = IdentityInfoRef.current.data1()
    let data2 = JobInfoRef.current.data2()
    let data3 = PersonalInfoRef.current.data3()
    if (data1 && data2 && data3) {
      let params = {
        address: {
          address: data3.address,
          city: data3.city,
          district: data3.district,
          province: data3.province,
        },
        auntHeadPhoto: data2.auntHeadPhoto,
        description: {
          description: data2.description
        },
        detail: {
          height: data3.height,
          weight: data3.weight,
          married: data3.married,
          education: data3.education,
          religion: data3.religion,
          visa: data3.visa,
          bloodType: data3.bloodType,
          children: data3.children,
          childrenAge: data3.childrenAge,
          spouseJob: data3.spouseJob,
          emergencyPhone: data3.emergencyPhone,
        },
        identity: data1,
        jobIntensionsList: data2.jobIntensionsList,
        jobStatus: data2.jobStatus,
        photoVideo: data2.photoVideo,
        skills: {
          cookAble: data2.cookAble,
          cuisine: data2.cuisine,
          language: data2.language,
          composite: data2.composite,
          auntCert: data3.auntCert,
          mandarinAble: data3.mandarinAble,
          maxExpectSalary: data3.maxExpectSalary,
          minExpectSalary: data3.minExpectSalary
        },
        workTime: data2.workTime,
        workType: data2.workType,
        workingExperiencesList: data3.workingExperiencesList,
        workingLife: data2.workingLife
      }
      Taro.showLoading({
        title: '修改中...'
      })
      props.dispatch({ type: 'resume/saveAunt', payload: params }).then(() => {
        Taro.hideLoading();
        Taro.showToast({
          title: '修改成功',
          icon: 'success'
        })
        const timer_back = setTimeout(() => {
          Taro.hideToast();
          timer_back && clearTimeout(timer_back)
          Taro.navigateBack().catch(() => {
            Taro.navigateTo({
              url: '/pages/resume/resume'
            })
          })
        }, 1000)
      }).catch(err => {
        Taro.hideLoading();
        tip.showTips(err.msg)
      })
    }
  }

  return (
    <View className='fixedView addResume index'>
      <NavBar title="编辑简历" />
      <AtTabs
        current={current} tabList={tabList}
        onClick={(value) => {
          setCurrent(value)
        }}
        className="BmTab" swipeable={false} animated={false} />
      <View className={classnames({ addResumeView: true, hideView: current !== 0 })}>
        <IdentityInfo onNextStep={nextStep} ref={IdentityInfoRef} resume={props.resume} dispatch={props.dispatch} />
      </View>
      <View className={classnames({ addResumeView: true, hideView: current !== 1 })}>
        <JobInfo onNextStep={nextStep} ref={JobInfoRef} resume={props.resume} dispatch={props.dispatch} />
      </View>
      <View className={classnames({ addResumeView: true, hideView: current !== 2 })}>
        <PersonalInfo onNextStep={nextStep} ref={PersonalInfoRef} resume={props.resume} />
      </View>
      {
        current == 2 ?
          <View className="fixedBtnView">
            <Button className="fixedBtn" onClick={submit} disabled={props.loading} loading={props.loading}>保存</Button>
          </View> :
          props.resume.fullscreen ?
            <View className="fixedBtnView">
              <Button className="fixedBtn" onClick={nextStep}>下一步</Button>
            </View> :
            <CoverView className="fixedBtnView">
              <Button className="fixedBtn" onClick={nextStep}>下一步</Button>
            </CoverView>
      }

    </View>
  )
})

export default connect(({ resume, dispatch, loading }) => ({
  resume,
  dispatch,
  loading: loading.effects['resume/saveAunt'],
}))(AddResume)
