import React, { FC, useState, forwardRef, useImperativeHandle } from 'react'
import { View, Button, Picker, Text } from '@tarojs/components'
import { AtForm, AtModal, AtModalHeader, AtModalContent, AtModalAction, AtIcon, AtTextarea } from 'taro-ui'
import { WorkingExperiencesItemType } from '../PersonalInfo'
import moment from 'moment'
import tip from '@/utils/tip'
import './index.less'

interface WorkingExperiencesProps {
  ref?,
  onConfirm?,
  resume?
}

interface formItemType {
  serviceName: number,
  startTime: string,
  endTime: string,
  experienceDiscription: string,
}

interface convertMap2ListType {
  name: string,
  id: string
}
interface editFunction {
  (value: WorkingExperiencesItemType): void;
}
export interface WorkingExperiencesRefType {
  show: Function,
  edit: editFunction
}

/**工作经历 */
const WorkingExperiences: FC<WorkingExperiencesProps> = forwardRef((props: WorkingExperiencesProps, ref: React.MutableRefObject<WorkingExperiencesRefType>) => {
  const initData: formItemType = {
    serviceName: 0,
    startTime: moment().format('YYYY-MM-DD'),
    endTime: moment().format('YYYY-MM-DD'),
    experienceDiscription: ''
  }
  // 抛出方法
  useImperativeHandle(ref, () => ({
    show: () => {
      setformItem(initData)
      setIsOpened(true)
      setIsAdd(true)
    },
    edit: (value: WorkingExperiencesItemType) => {
      setformItem({
        serviceName: props.resume.JobIntensionsList.findIndex((item: convertMap2ListType) => item.name == value.serviceName) || 0,
        startTime: moment(value.startTime).format('YYYY-MM-DD'),
        endTime: moment(value.endTime).format('YYYY-MM-DD'),
        experienceDiscription: value.experienceDiscription || ''
      })
      setIsOpened(true)
      setIsAdd(false)
    }
  }));
  let [formItem, setformItem] = useState<formItemType>(initData)
  let [isOpened, setIsOpened] = useState<boolean>(false)
  let [isAdd, setIsAdd] = useState<boolean>(true)

  //修改表单值
  function handleChange(name, value) {
    setformItem({ ...formItem, [name]: value })
  }
  function onClose() {
    setIsOpened(false)
  }
  function onConfirm() {
    if (moment(formItem.startTime).isAfter(formItem.endTime)) {
      tip.showTips('入职时间不得晚于离职时间')
      return
    }
    setIsOpened(false)
    let data = { ...formItem, serviceName: props.resume.JobIntensionsList.map((item: convertMap2ListType) => item.name)[formItem.serviceName] }
    isAdd ? props.onConfirm && props.onConfirm(data, true) : props.onConfirm && props.onConfirm(data, false)
  }
  function onTouchMove(e) {
    console.log(e)
    e.stopPropagation()
  }
  return (
    <View className="workingExperiences" onTouchMove={onTouchMove}>
      <AtModal isOpened={isOpened} onClose={onClose} className="BmModal">
        <AtModalHeader>工作经历</AtModalHeader>
        <AtModalContent>
          <View className='selectItem'>
            <Text className='title'>服务名称</Text>
            <Picker className="picker" onChange={(e) => { handleChange('serviceName', e.detail.value) }} value={formItem.serviceName} range={props.resume.JobIntensionsList} mode='selector' rangeKey="name">
              <View className='value'>
                {props.resume.JobIntensionsList.map((item: convertMap2ListType) => item.name)[formItem.serviceName]}
                <AtIcon value='chevron-right' size='16' color='#ccc'></AtIcon>
              </View>
            </Picker>
          </View>
          <View className='selectItem'>
            <Text className='title'>入职时间</Text>
            <Picker className="picker" mode='date' onChange={(e) => { handleChange('startTime', e.detail.value) }} value={formItem.startTime}>
              <View className='value'>
                {formItem.startTime}
                <AtIcon value='chevron-right' size='16' color='#ccc'></AtIcon>
              </View>
            </Picker>
          </View>
          <View className='selectItem'>
            <Text className='title'>离职时间</Text>
            <Picker className="picker" mode='date' onChange={(e) => { handleChange('endTime', e.detail.value) }} value={formItem.endTime} >
              <View className='value'>
                {formItem.endTime}
                <AtIcon value='chevron-right' size='16' color='#ccc'></AtIcon>
              </View>
            </Picker>
          </View>
        </AtModalContent>
        <View className="textareaView">{isOpened && <AtTextarea value={formItem.experienceDiscription} fixed={true} maxLength={1000} className="textarea" placeholder="请简短描述工作经历" onChange={(e) => { handleChange('experienceDiscription', e) }} />}</View>
        <AtModalAction> <Button onClick={onClose}>取消</Button> <Button onClick={onConfirm} className="confirmBtn">确定</Button> </AtModalAction>
      </AtModal>
    </View>
  )
})
export default WorkingExperiences
