import React, { FC, useState, forwardRef, useImperativeHandle } from 'react'
import { View, Button, Text } from '@tarojs/components'
import { AtModal, AtInput, AtModalHeader, AtModalContent, AtModalAction } from 'taro-ui'
import tip from '@/utils/tip'

interface onConfirmFunction {
  (value: string[]): void;
}

interface WishPriceProps {
  onConfirm: onConfirmFunction,
  ref?
}

interface showFunction {
  (value: string[]): void;
}
export interface WishPriceRefType {
  show: showFunction
}

interface formItemType {
  sPrice: string,
  ePrice: string,
}
/**个人情况 */
const WishPrice: FC<WishPriceProps> = forwardRef((props: WishPriceProps, ref: React.MutableRefObject<WishPriceRefType>) => {
  let [formItem, setformItem] = useState<formItemType>({
    sPrice: '',
    ePrice: ''
  })
  let [isOpened, setIsOpened] = useState<boolean>(false)

  // 抛出方法
  useImperativeHandle(ref, () => ({
    show: (value) => {
      setformItem({
        sPrice: value[0].toString(),
        ePrice: value[1].toString()
      })
      setIsOpened(true)
    }
  }));


  //修改表单值
  function handleChange(name, value) {
    setformItem({ ...formItem, [name]: value })
  }

  function onClose() {
    setIsOpened(false)
  }

  function onConfirm() {
    if (!formItem.sPrice || !formItem.ePrice) {
      tip.showTips('请输入期望薪资')
      return
    }
    if (parseInt(formItem.sPrice) > parseInt(formItem.ePrice)) {
      tip.showTips('最低价格不得高于最高价格')
      return
    }
    setIsOpened(false)
    props.onConfirm && props.onConfirm([formItem.sPrice, formItem.ePrice])
  }
  return (
    <View className="personalInfo">
      <AtModal isOpened={isOpened} onClose={onClose} className="BmModal">
        <AtModalHeader>期望薪资</AtModalHeader>
        <AtModalContent>
          {isOpened && <AtInput
            name='sPrice'
            title='最低期望'
            type='number'
            value={formItem.sPrice}
            cursor={formItem.sPrice.length}
            onChange={(value) => { handleChange('sPrice', value) }}
          ><Text className="primaryText">元</Text></AtInput>}
          {isOpened && <AtInput
            name='ePrice'
            title='最高期望'
            type='number'
            cursor={formItem.ePrice.length}
            value={formItem.ePrice}
            onChange={(value) => { handleChange('ePrice', value) }}
          ><Text className="primaryText">元</Text></AtInput>}
        </AtModalContent>
        <AtModalAction>
          <Button onClick={onClose}>取消</Button>
          <Button onClick={onConfirm} className="confirmBtn">确定</Button>
        </AtModalAction>
      </AtModal>
    </View>
  )
})
export default WishPrice
