import React, { FC, useState, useEffect, useImperativeHandle, forwardRef } from 'react'
import Taro from "@tarojs/taro"
import { View, Text, Picker } from '@tarojs/components'
import { AtInput, AtIcon } from 'taro-ui'
import classnames from 'classnames'
import common from '@/utils/common'
import Constant from '@/common/constant'
import Upload from '@/components/Upload'
import moment from 'moment'
import tip from '@/utils/tip'
import './index.less'

interface IdentityInfoProps {
  resume,
  onNextStep,
  ref,
  dispatch
}

interface formItemType {
  birthday: string,
  chineseZodiac: number,//生肖
  constellation: number,//星座
  idcard: string,
  name: string,
  nation: number,
  sex: number
  age: string,
  phone: string,
  imgUrl: string,

}
interface convertMap2ListType {
  name: string,
  id: string
}
export interface IdentityInfoRefType {
  data1: Function
}

/**第一步身份信息 */
const IdentityInfo: FC<IdentityInfoProps> = forwardRef((props: IdentityInfoProps, ref: React.MutableRefObject<IdentityInfoRefType>) => {
  let [formItem, setformItem] = useState<formItemType>({ phone: '', name: '', idcard: '', age: '', nation: 0, birthday: '', constellation: 0, chineseZodiac: 0, sex: 0, imgUrl: '' })
  let [more, setMore] = useState<boolean>(false)
  const SexArr: convertMap2ListType[] = common.convertMap2List(Constant.SexMap)
  // 抛出方法
  useImperativeHandle(ref, () => ({
    data1: () => submit()
  }));

  useEffect(() => {
    let { ResumeDetail } = props.resume;
    setformItem({
      imgUrl: ResumeDetail.identity?.imgUrl,
      phone: ResumeDetail.phone,
      name: ResumeDetail.identity?.name,
      idcard: ResumeDetail.identity?.idcard,
      age: ResumeDetail.identity?.age,
      nation: ResumeDetail.identity?.nation ? Constant.nationList.indexOf(ResumeDetail.identity?.nation) : 0,
      birthday: moment(ResumeDetail.identity?.birthday).format('YYYY-MM-DD'),
      constellation: ResumeDetail.identity?.constellation ? Constant.ConstellationArr.indexOf(ResumeDetail.identity?.constellation) : 0,
      chineseZodiac: ResumeDetail.identity?.chineseZodiac ? Constant.ChineseZodiacArr.indexOf(ResumeDetail.identity?.chineseZodiac) : 0,
      sex: ResumeDetail.identity?.sex ? SexArr.findIndex((item: convertMap2ListType) => { return item.id == ResumeDetail.identity?.sex }) : 0
    })
    if (ResumeDetail.identity?.idcard) {
      setMore(true)
    }
  }, [props.resume.ResumeDetail])

  function handleChange(name, value) {
    setformItem({ ...formItem, [name]: value })
  }

  function submit() {
    if (!formItem.name) {
      tip.showTips('请输入姓名')
      return false
    }
    if (!formItem.idcard) {
      tip.showTips('请输入身份证号')
      return false
    }
    const params = {
      ...formItem,
      sex: SexArr[formItem.sex].id,
      constellation: Constant.ConstellationArr[formItem.constellation],
      chineseZodiac: Constant.ChineseZodiacArr[formItem.chineseZodiac],
      nation: Constant.nationList[formItem.nation]
    };
    return params;
  }

  function uploadImgChange(res) {
    console.log(res)
    Taro.showLoading({ title: '识别中...' })
    props.dispatch({ type: 'resume/getIdCardFullInfo', payload: res }).then(data => {
      Taro.hideLoading()
      changeInfo(data)
    }).catch(err => {
      Taro.hideLoading()
      tip.showTips(err.msg)
    })
  }
  function handleChangeIdCard(value) {
    setformItem({ ...formItem, idcard: value });
    if (value.length == 18) {
      props.dispatch({ type: 'resume/getIdCardBaseInfo', payload: { idcard: value } }).then(data => {
        changeInfo(data)
      }).catch(err => {
        tip.showTips(err.msg)
      })
    }
  }
  function changeInfo(res) {
    setMore(true)
    res.name && setformItem((prev) => ({ ...prev, name: res.name }))
    res.imgUrl && setformItem((prev) => ({ ...prev, imgUrl: res.imgUrl }))
    setformItem((prev) => ({
      ...prev,
      idcard: res.cardNo,
      age: res.age,
      nation: res.nation ? Constant.nationList.indexOf(res.nation + '族') : 0,
      birthday: moment(res.birthday).format('YYYY-MM-DD'),
      constellation: res.constellation ? Constant.ConstellationArr.indexOf(res.constellation) : 0,
      chineseZodiac: res.chineseZodiac ? Constant.ChineseZodiacArr.indexOf(res.chineseZodiac) : 0,
      sex: SexArr.findIndex((item: convertMap2ListType) => item.name == res.sex) || 0
    }))

  }
  return (
    <View className='index'>
      <View className="identity">
        <Upload propsfileList={formItem.imgUrl} onChange={uploadImgChange} />
        <View className={classnames({
          bmform: true,
          heightAuto: more
        })} >
          <AtInput
            name='phone'
            title='手机号'
            type='number'
            placeholder='请输入手机号'
            value={formItem.phone}
            editable={false}
            onChange={(value) => { handleChange('phone', value) }}
          />
          <AtInput
            name='name'
            title='姓名'
            type='text'
            required
            placeholder='请输入姓名'
            value={formItem.name}
            onChange={(value) => { handleChange('name', value) }}
          />
          <AtInput
            name='idcard'
            title='身份证'
            required
            type='number'
            placeholder='请输入身份证'
            value={formItem.idcard}
            onChange={handleChangeIdCard}
          />
          <View className='selectItem disabled'>
            <Text className='title'>生日</Text>
            <View className='value'>
              {formItem.birthday ? formItem.birthday : <Text className="placeholder">请选择生日</Text>}
            </View>
          </View>
          <AtInput
            name='age'
            title='年龄'
            type='number'
            placeholder='请输入年龄'
            value={formItem.age}
            editable={false}
            onChange={(value) => { handleChange('age', value) }}
          />
          <View className='selectItem disabled'>
            <Text className='title'>性别</Text>
            <View className='value'>
              {formItem.sex != null ? SexArr.map((item: convertMap2ListType) => item.name)[formItem.sex] : <Text className="placeholder">请选择性别</Text>}
            </View>
          </View>
          <View className='selectItem disabled'>
            <Text className='title'>生肖</Text>
            <View className='value'>{Constant.ChineseZodiacArr[formItem.chineseZodiac]}</View>
          </View>
          <View className='selectItem disabled'>
            <Text className='title'>星座</Text>
            <View className='value'>{Constant.ConstellationArr[formItem.constellation]}</View>
          </View>
          <Picker onChange={(e) => { handleChange('nation', e.detail.value) }} value={formItem.nation} range={Constant.nationList} mode='selector'>
            <View className='selectItem'>
              <Text className='title'>民族</Text>
              <View className='value'>
                {Constant.nationList[formItem.nation]}
                <AtIcon value='chevron-right' size='16' color='#ccc'></AtIcon>
              </View>
            </View>
          </Picker>
        </View>
      </View >
    </View>
  )
})

export default IdentityInfo


